// IMPORTS
// Externals imports (Dependencies)
import axios from 'axios/index'

// Internal imports
import config from '../../config'
import action, { actionFailed } from '../../util/action'
import createAsyncConst from '../../util/createAsyncConst'

export const REMOVE_STORE = createAsyncConst('REMOVE_STORE')
export const SUBMIT_STORE = createAsyncConst('SUBMIT_STORE')
export const FETCH_STORE = createAsyncConst('FETCH_STORE')
export const EDIT_STORE = createAsyncConst('EDIT_STORE')

export function fetchUsers(page, filters = []) {
  return dispatch => {
    dispatch(action(FETCH_STORE.ACTION))
    return axios
      .get(`${config.API_URL}/store?filters=` + JSON.stringify(filters), {
        page,
        filters,
      })
      .then(resp => {
        dispatch(action(FETCH_STORE.SUCCESS, resp.data))
        return resp
      })
      .catch(e => dispatch(actionFailed(FETCH_STORE, e)))
  }
}

export function edit(id) {
  return dispatch => {
    dispatch(action(EDIT_STORE.ACTION, { id }))
    return axios
      .get(`${config.API_URL}/store/${id}`)
      .then(resp => resp.data.data.item)
      .then(data => dispatch(action(EDIT_STORE.SUCCESS, data)))
      .catch(e => dispatch(actionFailed(EDIT_STORE, e)))
  }
}

export function post(values) {
  return submit(values, 'post')
}

export function update(values) {
  return submit(values, 'put')
}

function submit(values, method) {
  const id = values.id ? `/${values.id}` : ''

  return dispatch => {
    dispatch(action(SUBMIT_STORE.ACTION))
    return axios[method](`${config.API_URL}/users${id}`, values)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(SUBMIT_STORE.SUCCESS, data))
        return data
      })
      .catch(e => dispatch(actionFailed(SUBMIT_STORE, e)))
  }
}

export function remove(id) {
  return dispatch => {
    dispatch(action(REMOVE_STORE.ACTION))
    return axios
      .delete(`${config.API_URL}/api/store/${id}`)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(REMOVE_STORE.SUCCESS, data))
        dispatch(fetchUsers())
      })
      .catch(e => actionFailed(REMOVE_STORE.FAILURE, e))
  }
}
